body {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  overflow-x: hidden;
}

:root {
  --black: #4b4a4a;
  --green: #017143;
  --white: #fff;
  --white2: #fff;
  --black2: #4b4a4a;
}

.dark {
  --black: #fff;
  --white: #113828;
  --black2: #124e36;
  height: 100vh;
}

section {
  position: relative;
  width: 100%;
  min-height: 100%;
  padding: 100px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: var(--white);
}

header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 10px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10000;
}

header .logo {
  position: relative;
  max-width: 70px;
}

header ul {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

header ul li {
  list-style: none;
  display: flex;
  margin-left: 30px;
}

header ul li a {
  display: inline-block;
  color: var(--black);
  font-weight: 500;
  text-decoration: none;
}

header ul li a:hover {
  color: var(--green);
  transition: all 0.2s ease-in-out;
}

.textBox__block {
  display: -webkit-box;
  -webkit-line-clamp: 8;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.content {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
}

.content .textBox {
  position: relative;
  max-width: 600px;
  z-index: 2;
  margin-right: 20px;
}

.content .textBox h2 {
  font-size: 3em;
  color: var(--black);
  line-height: 1.4em;
  font-weight: 700;
  margin-bottom: 10px;
}

.content .textBox h2 span {
  font-size: 1.2em;
  color: var(--green);
}

.content .textBox a {
  display: inline-block;
  margin-top: 20px;
  padding: 8px 20px;
  background: var(--black2);
  color: var(--white2);
  border-radius: 40px;
  font-weight: 500;
  letter-spacing: 1px;
  text-decoration: none;
}

.content .textBox a:hover {
  background: var(--green);
}

.content .textBox p {
  color: var(--black);
}

.dark .content .textBox h2 span {
  color: #00ff9b;
}

.content .imgBox {
  position: relative;
  width: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content .imgBox::before {
  content: "";
  position: absolute;
  width: 350px;
  height: 350px;
  background: var(--green);
  border-radius: 50%;
}

.content .imgBox img {
  position: relative;
  z-index: 1;
  max-width: 350px;
}

.sci {
  position: absolute;
  bottom: 40px;
  left: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sci li {
  list-style: none;
}

.sci li a {
  display: inline-block;
  margin-right: 15px;
  width: 50px;
  height: 50px;
  background: var(--black2);
  color: var(--white2);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}

.sci li a ion-icon {
  font-size: 1.6em;
}

.sci li a:hover {
  background: var(--green);
}

.themeSwitch {
  position: relative;
  width: 40px;
  height: 40px;
  background: var(--black);
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.themeSwitch ion-icon {
  font-size: 1.6em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.themeSwitch:hover {
  background: var(--green);
}

.themeSwitch.active {
  background: var(--white2);
}

.themeSwitch.active .moon {
  display: none;
}

.themeSwitch .sunny {
  display: none;
}

.themeSwitch .moon {
  display: flex;
  color: var(--white);
}

.themeSwitch.active .sunny {
  display: flex;
  color: var(--white);
}

@media (max-width: 991px) {
  section {
    padding: 150px 20px;
  }

  header {
    padding: 20px;
  }

  .sci {
    bottom: 20px;
    left: 20px;
  }
}
@media (max-width: 767px) {
  .content {
    flex-direction: column-reverse;
  }

  .content .textBox,
.content .imgBox {
    max-width: 100%;
    margin-right: 0px;
  }

  .content .imgBox img {
    max-width: 250px;
  }

  .content .imgBox::before {
    clip-path: circle(125px at center);
  }

  .content .textBox h2 {
    font-size: 1.75em;
  }
}
@media (max-width: 599px) {
  .navigation {
    display: none;
  }

  .navigation.active {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--white);
    flex-direction: column;
  }

  header ul li {
    margin: 10px 0px;
    margin-left: 0;
  }

  header ul li a {
    font-size: 1.5em;
    font-weight: 300;
  }

  .toggle {
    position: relative;
    width: 30px;
    height: 30px;
    background: url(../img/menu.webp);
    filter: invert(1);
    background-size: 30px;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    z-index: 1000;
  }

  .toggle.active {
    background: url(../img/close.webp);
    filter: invert(1);
    background-size: 25px;
    background-position: center;
    background-repeat: no-repeat;
  }

  .dark .toggle {
    filter: invert(0);
  }
}